#home {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 80vh;
    padding: 20px;
    margin-top: 70px;
  }

  #user-detail-photo {
    border-radius: 50%;
    width: 200px;
    height: 200px;
    object-fit: cover;
    margin-bottom: 2rem;
  }
  
  
  #user-detail-name {
    font-size: 4rem;
    font-weight: bold;
    margin-bottom: 1rem;
  }
  
  #user-detail-intro {
    font-size: 2rem;
    margin-bottom: 2rem;
    text-align: center;
  }
  