nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 10vh;
    background-color: #262626;
    color: white;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 9999;
  }
  
  nav ul {
    display: flex;
    justify-content: space-between;
    align-items: center;
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  nav ul li a {
    color: white;
    text-decoration: none;
    font-size: 1.2rem;
    font-weight: 500;
    margin: 0 1.5rem;
    position: relative;
  }
  
  nav ul li a::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 3px;
    bottom: -5px;
    left: 0;
    background-color: #4c4c4c;
    visibility: hidden;
    transform: scaleX(0);
    transition: all 0.3s ease-in-out 0s;
  }
  
  nav ul li a:hover::before {
    visibility: visible;
    transform: scaleX(1);
  }
  
  nav .logo {
    font-size: 1.5rem;
    font-weight: 600;
    margin-left: 1.5rem;
  }
  
  nav .nav-toggle {
    display: none;
  }
  
  nav .nav-toggle-label {
    position: absolute;
    top: 50%;
    right: 1.5rem;
    transform: translateY(-50%);
    z-index: 9999;
    cursor: pointer;
    display: none;
  }
  
  nav .nav-toggle-label span {
    display: block;
    width: 25px;
    height: 2px;
    background-color: white;
    margin-bottom: 5px;
    position: relative;
  }
  
  @media (max-width: 768px) {
    nav ul {
      flex-direction: column;
      position: fixed;
      top: 10vh;
      left: 0;
      width: 100%;
      background-color: #262626;
      overflow: hidden;
      transition: max-height 0.5s ease-out;
      max-height: 0;
    }
  
    nav ul li {
      width: 100%;
      text-align: center;
      margin: 0;
    }
  
    nav .nav-toggle-label {
      display: block;
    }
  
    nav .nav-toggle:checked + ul {
      max-height: 100vh;
    }
  
    nav ul li a {
      margin: 1rem 0;
    }
  }
  